body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

form input {
  border: none;
  border-radius: 4px;
  flex-basis: 15%;
  min-width: 120px;
  height: 28px;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
  box-shadow: 0 0 9px 9px #dfd4b3;
  margin: 10px;
  font-size: 20px;
}
input:focus {
  background-color: whitesmoke;
  box-shadow: 0 0 12px 12px #ff005c;
}
h2 {
  cursor: context-menu;
}

p span {
  font-weight: bold;
}
hr {
  background-color: #ff005c;
  height: 2px;
  border-radius: 6px;
  border: none;
}

.app {
  box-sizing: border-box;
  position: absolute;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  text-align: center;
  padding: 0 10px;
  color: white;
  background: linear-gradient(45deg, #ff005c 0%, #ffd4b3 100%);
  cursor: context-menu;
  overflow-y: scroll;
}
a {
  color: white;
  text-decoration: none;
  -webkit-transition: 0.2s linear;
  transition: 0.2s linear;
}
a:hover {
  color: rgb(95, 2, 2);
}
@media (min-width: 768px) {
  .app {
    font-size: 25px;
  }
}

